<template>
  <div class="signup">
      <form action="/new/signup" method="post">
    <input type="text" name="username" id="user" placeholder="用户名"/>
    <input type="password" name="password" id="password" placeholder="密码"/>
    <input type="submit" id="submit" class="login" title="注册"/>
    </form>
  </div>
</template>

<script>
export default {
  name: 'SignUp',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  }
}
</script>

