<template>
  <div class="compose">
      <link rel="stylesheet" href="https://unpkg.com/element-ui/lib/theme-chalk/index.css">
        <link rel="stylesheet" href="https://use.fontawesome.com/releases/v5.0.10/css/all.css" integrity="sha384-+d0P83n9kaQMCwj8F4RJB66tzIwOKmrdb46+porD/OvrJ+37WqIM7UoBtwHO6Nlg" crossorigin="anonymous">

      <el-container>


          <!-- header -->
          <el-header>
              <header class="top-bar">
            <div class="top-btn-wrapper">
                <a href="home"><i class="fas fa-chevron-left" style="font-size: 1.5rem; float:left;"></i></a>
            </div>
            <div class="top-btn-wrapper">
                <a :href="'profile/' + user_id"><div class="avatar"></div></a>
            </div>
            <div class="top-btn-wrapper">
                <div v-on:click="send" style="float: right;"><a class="send-btn" v-bind:class="{disabled: message.length==0}">发送</a></div>
            </div>
            </header>
            </el-header>

            <!-- main -->
          <el-main>
            <el-input class="post"
                type="textarea"
                :autosize="{ minRows: 2, maxRows: 4}"
                placeholder="请输入内容"
                v-model="message">
            </el-input>

            <!-- 图片九宫格 -->
            <div class="img-wrapper">
                <div v-for="img in imgs"
                v-bind:key="img.id" class="img">
                <svg><path d="M27.243 36l14.88-14.88c1.17-1.17 1.17-3.07 0-4.24-1.172-1.173-3.072-1.173-4.243 0L23 31.757 8.122 16.878c-1.17-1.17-3.07-1.17-4.242 0-1.172 1.172-1.172 3.072 0 4.243L18.758 36 3.878 50.88c-1.17 1.17-1.17 3.07 0 4.24.587.587 1.355.88 2.123.88s1.536-.293 2.122-.88L23 40.243l14.88 14.88c.585.585 1.353.878 2.12.878.768 0 1.535-.293 2.12-.88 1.173-1.17 1.173-3.07 0-4.24L27.244 36z"></path></svg>
                </div>
                <label for="selectphoto" v-if="imgs.length < 9 && imgs.length > 0"><i class="far fa-image"></i></label>
            </div>
              </el-main>


              <!-- footer -->
          <el-footer>
                  <!-- 图片 -->
                    <div class="footer-btn">
                        <input id="selectphoto" name="uploader" type="file" style="display:none" accept="image/gif,image/jpeg,image/webp,image/jpg,image/png,image/bmp" multiple="multiple" @change="new_upload">
                        <label for="selectphoto"><i class="far fa-image"></i></label>
                    </div>
                    <!-- 表情 -->
                    <div class="footer-btn">
                        <i class="far fa-smile"></i>
                    </div>
                    <span class="limit" v-if="message.length > 0">{{message.length}}</span>

              
              </el-footer>




          </el-container>
      
    </div>
</template>

<script>
export default {
  name: 'Compose',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      message: '',
      imgs: [],
    }
  },
  methods: {
        new_upload: function (e) {
            var _this = this;
            var file = e.target.files[0];
            var reader = new FileReader();
            reader.onload = (function (file) {
                return function (e) {
                    _this.imgs.push(this.result);
                };
            })(e.target.files[0]);
            reader.readAsDataURL(e.target.files[0]);
        },
        send: function (e) {
            alert('WTF');
            var data = {
                'imgs': this.imgs
            }
            var xhr = new XMLHttpRequest();
            xhr.open('POST', '/new/compose');
            var params = 'text=' + this.message + '&base64str=' + this.imgs[0];
            xhr.setRequestHeader("Content-type", "application/x-www-form-urlencoded");
            xhr.send(params);
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
header.top-bar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 2.75rem;
  width: 100%;
  box-sizing: border-box;
  padding: 0;
  background-color: #fafafa;
  color: #262626;
  border-top-width: 0;
  border-right-width: 0;
  border-bottom-width: 1px;
  border-left-width: 0;
  border-color: #cfcfcf;
  border-style: solid;
  font-size: 1rem;
  z-index: 995;
}
.top-btn-wrapper {
    float: left;
    width: 33.3%;
    height: 100%;
}
.avatar {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background-image: url(../../static/images/avatar.jpg);
    background-size: cover;
    display: block;
    margin:0 auto;
}
.footer-btn {
    float: left;
}
.limit {
    float: right;
}
.send-btn {
    min-width: 0;
    margin-left: .75rem;
    margin-right: .001rem;
    height: 1.6875rem;
    line-height: 1.6875rem;
    color: #1b9af4;
}
.send-btn.disabled {
    opacity: .5;
}
.post {
    font-size: 1.5rem;
    border: none;
}
.post:focus {
    outline: none;
}

.img-wrapper {

}
.img {

}

.bottom-bar {
  position: fixed;
  bottom: 0;
  height: 2.75rem;
  font-size: 1rem;
  z-index: 995;
}
</style>



